/*===Mazorca Core===*/
/* Eric Meyer's Reset CSS v2.0 - http://cssreset.com */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

/**
* Basic Styles
* ---------------
* Edítalos o sobreescríbelos
*
*/
/**
 * Super basic stylings
 *
 */
* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

html {
  height: 100%;
}

html, body {
  min-height: 100%;
}

body {
  position: relative;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  font-family: BlissPro;
}

strong {
  font-weight: bold;
}

img {
  -ms-interpolation-mode: bicubic;
}

.column img, .columns img {
  max-width: 100%;
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
}

p em {
  font-style: italic;
}

section, article, aside, header, footer, nav, figure, figcaption {
  display: block;
}

time, mark {
  display: inline-block;
}

/**
 * All helpers use the !important keyword, if ever necessary their
 * output might be overriden with the !important keyword in a
 * property located lower in the cascade.
 *
 */
/**
 *	Helpers Index:
 *		Position
 *		Display
 *		Gutters
 *		Paddings and Margins
 *		JS Classes
 *
 */
/*************************** Position ***************************/
.absolute {
  position: absolute !important;
}

.relative {
  position: relative !important;
}

.fixed {
  position: fixed !important;
  max-width: 100% !important;
  width: 100% !important;
  left: 0;
}

.leer-mas__container {
  position: relative;
  overflow: hidden;
}

.flip {
  -moz-transform: rotate(180deg) translateX(-50%) !important;
  -o-transform: rotate(180deg) translateX(-50%) !important;
  -webkit-transform: rotate(180deg) translateX(-50%) !important;
  transform: rotate(180deg) translateX(-50%) !important;
}

.centerXY {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.text-center {
  text-align: center !important;
}

.flex {
  display: flex !important;
}

.left {
  float: left !important;
}

.right {
  float: right !important;
}

.top56 {
  top: 56px;
}

.ontop {
  z-index: 99 !important;
}

.z-0 {
  z-index: 0 !important;
}

.w100 {
  width: 100% !important;
}

.h100 {
  height: 100% !important;
}

.op1 {
  opacity: 1 !important;
}

.tdn {
  text-decoration: none !important;
}

.text-overflow-ellipsis {
  text-overflow: ellipsis !important;
  overflow: hidden !important;
  white-space: nowrap !important;
}

/*************************** Layout ***************************/
.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}

/*************************** Display ***************************/
.d-bl {
  display: block !important;
}

.inline {
  display: inline !important;
}

.inline-block {
  display: inline-block !important;
}

/*************************** Gutters ***************************/
/*************************** Paddings and Margins ***************************/
.p0 {
  padding: 0 !important;
}

.pb0 {
  padding-bottom: 0 !important;
}

.pr0 {
  padding-right: 0 !important;
}

.pl0 {
  padding-left: 0 !important;
}

.m0 {
  margin: 0 !important;
}

.mb0 {
  margin-bottom: 0 !important;
}

.mb40 {
  margin-bottom: 40px !important;
}

/*************************** Arrows ***************************/
.icon-arrow-up:after {
  content: "";
}

.icon-arrow-down:after {
  content: "";
}

/*************************** Drags, Selects ***************************/
.undraggable-unselectable-cascading *,
.undraggable-unselectable {
  user-drag: none !important;
  -webkit-user-drag: none !important;
  user-select: none !important;
  -moz-user-select: none !important;
  -webkit-user-select: none !important;
  -ms-user-select: none !important;
}

.unselectable {
  user-select: none !important;
  -moz-user-select: none !important;
  -webkit-user-select: none !important;
  -ms-user-select: none !important;
}

/*************************** Text ***************************/
.ttu {
  text-transform: uppercase !important;
}

.ttn {
  text-transform: none !important;
}

/*************************** Animations ***************************/
.transition-fast {
  transition: all 0.3s;
}

.transition-slow {
  transition: all 1s;
}

.hover-scale-up:hover {
  transform: scale(1.2) !important;
}

/*************************** JS Classes ***************************/
[v-cloak] {
  opacity: 0;
}

.noScroll_JS, .no-scroll {
  overflow: hidden !important;
}

/*************************** Bootstrap ***************************/
.hidden {
  display: none !important;
}

