*   Feel free to play with them!
*/
/*Set to "true" for -webkit-,-moz-appereance:none
   useful for editing radio buttons amog other things*/
/*similar as above but for selects*/
/*
*   If you need to style basic layout and stuff go to your
*   "modules/_contactForm.scss" or similar file
*/
/*Input Elements' Params*/
input, textarea {
  background-color: transparent;
  border: 0px none transparent;
  border-radius: initial;
  padding: 10px;
  -moz-appearance: none;
  -webkit-appearance: none;
}

input, textarea {
  width: 100%;
}

textarea {
  resize: vertical;
}

input[type=submit],
input[type=button] {
  background-color: #ffbe2c;
  display: block;
}

input[type=date],
input[type=time],
input[type=button] {
  width: auto;
}

input::-webkit-datetime-edit {
  color: black;
}

input::-webkit-inner-spin-button,
input::-webkit-calendar-picker-indicator {
  cursor: pointer;
  opacity: 1;
}

input[type=checkbox],
input[type=radio] {
  cursor: pointer;
  padding: 0;
  height: 1rem;
  width: 1rem;
  vertical-align: text-top;
}
input[type=checkbox]:checked,
input[type=radio]:checked {
  background-color: black;
}

input[type=radio] {
  border-radius: 100%;
}

select {
  -moz-appearance: none;
  -webkit-appearance: none;
  cursor: pointer;
}

input:focus, select:focus, textarea:focus, button:focus {
  outline: none;
}

.label {
  font-family: "OpenSans", sans;
  font-size: 14px;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
}

